(function($){
	/*main-menu
	===========================================*/
	$('.js-dropdown-arrow').on('click', function(e){
		e.preventDefault();
		var attribute = $(this).attr('href');
		$(attribute).slideToggle();

		var dataAttr = $(this).attr('data-target');
		$(dataAttr).slideToggle(500);
	});

	/* main-slider
	===============================================*/
	$('#js-main-slider').slick({
		  infinite: true,
		  speed: 3000,
		  autoplay: true,
		  autoplaySpeed: 2000,
		  dots: true,
		  mobileFirst: true,
		  pauseOnHover: false,
		  fade: true
	});

	/* sidebar-slider
	======================================*/

	$('.js-sidebar-slider').slick({
		infinite: true,
		  speed: 2000,
		  autoplay: true,
		  autoplaySpeed: 2000,
		  dots: true,
		  mobileFirst: true,
		  pauseOnHover: false,
		  arrows: false
	});

	/* bottom slider */

	$('#bottom-slider').slick({
		infinite: true,
		  speed: 2000,
		  autoplay: true,
		  autoplaySpeed: 2000,
		  mobileFirst: true,
		  pauseOnHover: false,
		  arrows: true,
		  slidesToShow: 9,
		  slidesToScroll: 5
	});

	/* show more */


	$('.show-more-link').on('click', function(e){
		e.preventDefault();
		var showMoreId = $(this).attr('href');
		$(showMoreId).fadeToggle();
		
	});

	/* wow.js
	=================================*/

	var wow = new WOW(
  {
    boxClass:     'wow',      // animated element css class (default is wow)
    animateClass: 'animated', // animation css class (default is animated)
    offset:       150,          // distance to the element when triggering the animation (default is 0)
    mobile:       false,       // trigger animations on mobile devices (default is true)
    live:         true,
    scrollContainer: null // optional scroll container selector, otherwise use window
  }
);
wow.init();

})(jQuery)